import React, { Component } from 'react'
import {
    Card,
    Modal,
    Button,
    Table,message
} from 'antd'
import {formateDate} from "../../utils/dateUtils"
import LinkButton from "../../components/link-button/LinkButton"
import {reqDeleteUser, reqUsers, reqAddOrUpdateUser} from "../../api/index";
import UserForm from './UserForm';



export default class User extends Component {
    state={
        users:[],
        roles:[],
        isShow:false
    }

    initColumns = () => {
        this.columns = [
          {
            title: '用户名',
            dataIndex: 'username'
          },
          {
            title: '邮箱',
            dataIndex: 'email'
          },
    
          {
            title: '电话',
            dataIndex: 'phone'
          },
          {
            title: '注册时间',
            dataIndex: 'create_time',
            render: formateDate
          },
          {
            title: '所属角色',
            dataIndex: 'role_id',
            render:(role_id) => this.roleNames[role_id]
          },
          {
            title: '操作',
            render: (user) => (
              <span>
                <LinkButton onClick={()=>this.showUpdate(user)}> 修改</LinkButton>
                <LinkButton onClick={() => this.deleteUser(user)} >删除</LinkButton>
              </span>
            )
          },
        ]
      }

      /*
  根据role的数组, 生成包含所有角色名的对象(属性名用角色id值)
   */
  initRoleNames = (roles) => {
    const roleNames = roles.reduce((pre, role) => {
      pre[role._id] = role.name
      return pre
    }, {})
    // 保存
    this.roleNames = roleNames
  }

  /*
  删除指定用户
   */
  deleteUser = (user) => {
    Modal.confirm({
      title: `确认删除${user.username}吗?`,
      onOk: async () => {
        const result = await reqDeleteUser(user._id)
        if(result.status===0) {
          message.success('删除用户成功!')
          this.getUsers()
        }
      }
    })
  }
  
  //显示修改界面
  showUpdate=(user)=>{
      this.user=user;//保存user
      this.setState({
          isShow:true
      })
  }

  showAdd=()=>{
      this.user=null;
      this.setState({
          isShow:true
      })
  }
   /*
  添加/更新用户
   */
  addOrUpdateUser = async () => {

    this.setState({isShow: false})

    // 1. 收集输入数据
    const user = this.refs.adddata.state;
    console.log(user);
    // 如果是更新, 需要给user指定_id属性
    if (this.user) {
      user._id = this.user._id
    }

    // 2. 提交添加的请求
    const result = await reqAddOrUpdateUser(user)
    // 3. 更新列表显示
    if(result.status===0) {
      message.success(`${this.user ? '修改' : '添加'}用户成功`)
      this.getUsers()
    }
  }
      getUsers = async () => {
        const result = await reqUsers()
        if (result.status===0) {
          const {users, roles} = result.data
          this.initRoleNames(roles)
          this.setState({
            users,
            roles
          })
        }
      }
    
      componentWillMount () {
        this.initColumns()
      }
      componentDidMount(){
          this.getUsers()
      }
    render() {
        const title = (<span>
            <Button type="primary"  onClick={this.showAdd} >创建用户</Button> 
        </span>)
        const{users,isShow,roles}=this.state;
        const user=this.user||{};
        return (
           <Card title={title}>
              <Table 
              bordered={true} 
              rowKey='_id' 
              dataSource={users} 
              columns={this.columns} 
              pagination={{ defaultPageSize: 5, showQuickJumper: true }}
        />
          <Modal
          title={user._id?"修改用户":"添加用户"}
          visible={isShow}
          onOk={this.addOrUpdateUser}
          onCancel={()=>{this.setState({
              isShow:false
          })}}
        >

             <UserForm roles={roles} ref="adddata" user={user}  ></UserForm>
        </Modal>
           </Card>
        )
    }
}
